/* error */
#main {
    display: table;
    width: 100%;
    height: 80vh;
    text-align: center;
  }
  
  .fof {
    display: table-cell;
    vertical-align: middle;
  }
  
  .fof h1 {
    font-size: 196px;
    display: inline-block;
    padding-right: 12px;
    -webkit-animation: type 0.5s alternate infinite;
            animation: type 0.5s alternate infinite;
    transition: all 0.6s;
    color: #fff;
  }
  
  .fof p {
    font-size: 106px;
    display: inline-block;
    padding-right: 12px;
    -webkit-animation: type 0.5s alternate infinite;
            animation: type 0.5s alternate infinite;
    transition: all 0.6s;
    color: #fff;
  }
  
  @-webkit-keyframes type {
    from {
      box-shadow: inset -3px 0px 0px #888;
    }
    to {
      box-shadow: inset -3px 0px 0px transparent;
    }
  }
  
  @keyframes type {
    from {
      box-shadow: inset -3px 0px 0px #888;
    }
    to {
      box-shadow: inset -3px 0px 0px transparent;
    }
  }
